const createSortationContainerBlock = (count: number) => {
  return createBlock('SortationContainer', [
    createBlock('Text'),
    createBlock('SortationAnswers', [
      ...repeat(
        () => createBlock('SortationAnswer', [createBlock('Text')]),
        count
      ),
    ]),
    createBlock('Text'),
  ]);
};

const createSortationFeedbackBlock = type => {
  return createBlock(
    'Feedback',
    [
      createBlock('Image', [], {
        props: {
          src: type === 'Correct' ? Correct_default : Incorrect_default,
        },
      }),
      createBlock('Text'),
      createEditableBtn({ label: 'Continue' }),
    ],
    {
      props: {
        type,
      },
    }
  );
};

