/** @jsx jsx */

import React, {
  useContext,
  useState,
  useRef,
  useEffect,
  useLayoutEffect,
  memo,
} from 'react';
import { createPortal } from 'react-dom';
import { css, jsx } from '@emotion/core';
import ie11VerticalFlexCenterFix from 'services/ie11VerticalFlexCenterFix';
import getStore from 'services/getStore';
import pubSub from 'services/pubSub';
import checkReturnToInteractionEditing from 'services/checkReturnToInteractionEditing';
import getBlockById from 'services/apiHelpers/getBlockById';
import { useSelector, useDispatch } from 'react-redux';
import setCurrentBlock from 'services/apiHelpers/setCurrentBlock';
import updateReadOnlyText from 'services/updateReadOnlyText';
import { motion } from 'framer-motion';
import { InteractionContext } from 'components/Layouts/ContainerLogical';
import { killAllAudio } from 'services/killAllAudio';
import { isIOSSafari } from 'services/isIOSSafari';

const store = getStore();

let container;
let modalOpen = false;

interface IModal {
  useFader?: boolean;
  disableNullSpaceClick?: boolean;
  minWidth?: string;
  onClose?: () => void;
  onOpened?: () => void;
  children: any;
}

const Modal = memo(
  ({
    useFader = true,
    disableNullSpaceClick = false,
    minWidth = '50vw',
    onClose,
    onOpened = () => {},
    children,
  }: IModal) => {
    if (!container) {
      container = document.querySelector('#modal-container');
    }

    const interactionContext = useContext(InteractionContext);

    const [btnColor, setBtnColor] = useState<string | undefined>(undefined);

    const focusedNodeIdOnOpen = useSelector<any>(
      ({ focusedNodeId }) => focusedNodeId
    );

    const rootContainer = useRef<HTMLDivElement>(null);
    const contentContainer = useRef<HTMLDivElement | null>(null);
    const hiddenButton = useRef(null);
    const buttonTabIndex = useRef(0);
    const checkResize = useRef(false);

    const getButtonAsAnchorOrButton = (
      container: HTMLElement
    ): HTMLAnchorElement | HTMLButtonElement | null => {
      if (!container) {
        return null;
      }

      return (
        container.querySelector('[class*="navigationButton"]') ||
        container.querySelector('[class*="genericButton"]') ||
        null
      );
    };

    useEffect(() => {
      modalOpen = true;
      window['MODAL_OPEN'] = true;

      if (!contentContainer.current) {
        return;
      }

      const getAllModalContent = () => {
        let allModalContent: any = contentContainer?.current?.querySelectorAll(
          '.chameleon-text, a, .chameleon-image img, .video-player-container, [class*="Audio"] img, audio, svg, [id*="navigation-button"], [id*="generic-button"]'
        );
        // , button < capture the nullSpace button ?

        if (allModalContent) {
          allModalContent = Array.from(allModalContent) as HTMLElement[];
        } else {
          return [];
        }

        allModalContent.forEach(entry => {
          entry.setAttribute('tabindex', '0');
        });

        return allModalContent;
      };

      const allModalContent = getAllModalContent();

      if (!allModalContent.length) {
        return;
      }

      // give focus to first chameleon-text if it exists
      // same behaviour as previous
      const firstChameleonTextElement = allModalContent.find(entry => {
        return entry?.classList && entry.classList.contains('chameleon-text');
      });

      if (firstChameleonTextElement) {
        firstChameleonTextElement.focus();
      } else {
        allModalContent[0].focus();
      }

      const tabOveride = e => {
        // we get these again to be sure a fresh collection
        // if any element hasn't yet loaded
        const allModalContent = getAllModalContent();
        const firstContentElement = allModalContent[0];
        const lastContentElement = allModalContent[allModalContent.length - 1];

        if (!['Tab'].includes(e.key)) {
          return;
        }

        const reverseTab = e.shiftKey;

        if (e.target === firstContentElement && reverseTab) {
          e.preventDefault();
          lastContentElement.focus();
          //
          return;
        }

        if (e.target === lastContentElement && !reverseTab) {
          e.preventDefault();
          firstContentElement.focus();
        }
      };

      contentContainer.current.addEventListener('keydown', tabOveride);

      let button = allModalContent[allModalContent.length - 1];
      if (button) {
        // if (button && !button.hasAttribute('data-custom-label')) {
        //   button.setAttribute('aria-label', 'Close dialog');
        // }

        const buttonHasTitleOrCustomLabel = ['title', 'data-custom-label'].some(
          entry => {
            const buttonHasAttribute = button.hasAttribute(entry);
            // console.log({ entry, buttonHasAttribute });

            return buttonHasAttribute;
          }
        );

        if (!buttonHasTitleOrCustomLabel) {
          button.setAttribute('aria-label', 'Close dialog');
        }
      }

      // give ALL other elements aria-hidden ???
      const root = document.querySelector('#root')!;
      root.setAttribute('aria-hidden', 'true');

      document.body.style.overflowY = 'hidden';

      const onResize = () => {
        if (!rootContainer.current) return;

        rootContainer.current.style.alignItems =
          window.innerHeight < contentContainer.current!.scrollHeight
            ? 'flex-start'
            : 'center';

        checkResize.current = true;
      };

      const tick = setInterval(() => {
        onResize();
        window.addEventListener('resize', onResize);
        if (checkResize.current) clearInterval(tick);
      }, 100);

      return () => {
        clearInterval(tick);

        modalOpen = false;
        window['MODAL_OPEN'] = false;

        root.setAttribute('aria-hidden', 'false');
        document.body.style.overflowY = 'auto';

        checkResize.current = false;
        window.removeEventListener('resize', onResize);

        contentContainer?.current?.removeEventListener('keydown', tabOveride);
      };
    }, []);

    useEffect(() => {
      if (!window.dev) {
        // we're opening the modal from the editor so we don't need these checks
        return;
      }

      pubSub.subscribe('CLOSE_MODAL', $ => {
        onClose!();
      });

      return () => {
        const { focusedNodeId } = store.getState();

        if (!focusedNodeId) {
          return;
        }

        const block = getBlockById(focusedNodeId);

        const isNotInteraction = [
          'text',
          'image',
          'video',
          'svg',
          'embed',
          'editablebutton',
          'continuebutton',
        ].includes(block.type.toLowerCase());

        if (isNotInteraction) {
          if (focusedNodeIdOnOpen) {
            setCurrentBlock({ id: focusedNodeIdOnOpen });
          } else {
            setCurrentBlock(null);
          }
        }
      };
    }, []);

    useLayoutEffect(() => {
      if (window.dev && !window.dev()) {
        updateReadOnlyText(contentContainer.current);
      }
    }, []);

    useEffect(() => {
      if (window.dev && window.dev()) {
        return;
      }

      // find all the hrefs a

      const links = Array.from(contentContainer.current!.querySelectorAll('a'));

      if (!links.length) {
        return;
      }

      const closeOnLinkClick = e => {
        onClose!();
      };

      const addRemoveCloseOnLinkClick = addOrRemove => {
        links.forEach(link => {
          if (link.href.includes('#')) {
            link[`${addOrRemove}EventListener`]('click', closeOnLinkClick);
          }
        });
      };

      addRemoveCloseOnLinkClick('add');

      return () => {
        addRemoveCloseOnLinkClick('remove');
      };
    }, []);

    useEffect(() => {
      // ' proof of concept '

      if (interactionContext?.stopAudioWhenModalIsOpened) {
        killAllAudio();
      }

      //
    }, []);

    // console.log('modal renders');

    return createPortal(
      <motion.div
        ref={rootContainer}
        initial={{ opacity: 0 }}
        transition={{
          duration: useFader ? 0.325 : 0,
          ease: 'easeOut',
        }}
        animate={{ opacity: 1 }}
        onAnimationComplete={onOpened}
        css={css`
          position: fixed;
          display: flex;
          justify-content: center;
          align-items: center;
          left: 0px;
          top: 0px;
          width: 100vw;
          min-height: 100vh;
          max-height: 100%;
          padding: ${isIOSSafari() ? '65px' : '10px'} 10px;
          background: rgba(0, 0, 0, 0.6);
          z-index: 1;
          ${ie11VerticalFlexCenterFix('height: 100vh;')}

          overflow-y: scroll;
        `}
      >
        <button
          ref={hiddenButton}
          css={css`
            position: absolute;
            left: 3px;
            top: 3px;
            width: calc(100% - 6px);
            height: calc(100% - 6px);

            border: none;
            outline: none;
            background: transparent;

            &:focus {
              border: 1px solid ${btnColor || '#282a2b'};
            }

            ${disableNullSpaceClick &&
              `
              pointer-events: none;
          `}
          `}
          onClick={(e: any) => {
            if (disableNullSpaceClick) {
              return;
            }
            //
            onClose!();
          }}
          aria-hidden="true"
        ></button>

        <div
          ref={contentContainer}
          css={css`
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            max-width: 1024px;
            border-radius: 50px;
            background: #fff;
            box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.2);
          `}
          onClick={e => e.stopPropagation()}
        >
          {children}
        </div>
      </motion.div>,
      container
    );
  },
  () => {
    // if it doesn't exist
    if (!window.dev) {
      return false;
    }

    // console.log(
    //   'memo intends to limit renders of modal in output :',
    //   !window.dev()
    // );

    return !window.dev();
  }
);

const useModal = ({ children }) => {
  const [, setState] = useState(true);

  return <Modal onClose={() => setState(false)}>{children}</Modal>;
};

export default Modal;

export { useModal };
